				description:`This tour is suited for first-time visitors, families (non-strenuous hiking) and individual travelers and is a once-in-a-lifetime experience - a must-do when visiting Peru!
From Cusco, you'll be transferred to the railway station to enjoy your 2-hour scenic rail tour to Aguas Calientes. You have the option to upgrade to the The Vistadome panoramic rail service which is the most comfortable way to journey to Machu Picchu.
The renovated Vistadome railcars have beautiful panoramic windows to make the most of the spectacular scenery and allow for fantastic photo opportunities. Delicious meals as well as hot and cold refreshments are available on the way.
Once we have arrived at Aguas Calientes station we'll board a bus and head up to the ruins of the "sacred city" known as Machu Picchu. Your expert guide will walk with you through the main streets of these amazing ruins. Stroll through town squares and climb delicately carved stone stairways.
Machu Picchu holds a deep spirituality in it's buildings and surrounding formations - we guarantee you ample time to embrace this magnificent wonder and steep in it's ancient glory. Hiking these ruins is sure to work up an appetite so there will be a buffet lunch waiting for you. In the afternoon you'll return to the train station in Aguas Calientes for the trip back to Cusco.`,
